.reviews {
  width: 375px;
  margin: 0 auto;
}

.reviews__container {
  position: relative;
  margin-left: 20px;
}

.reviews__image img {
  margin-top: 70px;
}

.reviews__arrow-back {
  position: absolute;
  top: 203px;
  left: 264px;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.reviews__arrow-back::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 11px;
  width: 0;
  height: 0;
  border: 6px solid;
  border-color: white;
  border-left: 0;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
}

.reviews__arrow-next {
  position: absolute;
  top: 203px;
  left: 304px;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.reviews__arrow-next::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 13px;
  width: 0;
  height: 0;
  border: 6px solid;
  border-color: white;
  border-right: 0;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
}

.reviews__header {
  margin: 0;
  margin-top: 20px;
  font-size: 30px;
  font-weight: normal;
  line-height: 1.33;
  color: #010101;
}

.reviews__text {
  margin: 0;
  margin-top: 5px;
  font-size: 18px;
  font-weight: normal;
  line-height: 1.33;
}

.reviews__description {
  margin: 0;
  margin-top: 20px;
  margin-bottom: 10px;
  width: 335px;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.88;
}

.reviews__open {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.88;
  color: #f78e6a;
}

.reviews__button {
  position: relative;
  margin-top: 65px;
  margin-bottom: 65px;
}

.reviews__button a {
  padding: 20px 51px 20px 80px;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  color: white;
  border-radius: 29px;
  box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.1);
  background-color: #f78e6a;
}

.reviews__button::before {
  content: "";
  position: absolute;
  top: -22px;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background-image: linear-gradient(to right, #f78e6a, #fc66a2);
}

.reviews__button a::after {
  content: "";
  position: absolute;
  left: 28px;
  width: 2px;
  height: 16px;
  background-color: white;
}

.reviews__button a::before {
  content: "";
  position: absolute;
  left: 28px;
  width: 2px;
  height: 16px;
  transform: rotate(90deg);
  background-color: white;
}

.reviews__title,
.reviews__list,
.reviews__image-background,
.reviews__description--desktop {
  display: none;
}

@media (min-width: $desktop-width) {
  .reviews {
    width: 100%;
  }

  .reviews__container {
    display: flex;
    margin-top: 144px;
    margin-left: 169px;
  }

  .reviews__title,
  .reviews__list,
  .reviews__image-background,
  .reviews__description--desktop {
    display: block;
  }

  .reviews__arrow-back,
  .reviews__arrow-next,
  .reviews__open,
  .reviews__description span {
    display: none;
  }

  .reviews__title {
    margin-top: 45px;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.67;
    color: #f78e6a;
    background-clip: text;
    background-image: linear-gradient(to bottom, #f78e6a, #fc66a2), linear-gradient(to bottom, #f78e6a, #f78e6a);
    -webkit-text-fill-color: transparent;
  }

  .reviews__list {
    margin: 0;
    margin-left: 10px;
    padding: 0;
  }

  .reviews__item {
    width: 160px;
    list-style: none;
    font-size: 18px;
    font-weight: normal;
    line-height: 2.3;
    color: rgba(0, 0, 0, 0.5);

    &--active {
      color: #000000;
    }
  }

  .reviews__image-background {
    margin-top: -60px;
    margin-left: -270px;
    margin-right: 37px;
  }

  .reviews__image img {
    margin-top: 0;
    margin-right: 40px;
  }

  .reviews__block {
    display: flex;
  }

  .reviews__header {
    margin-top: 95px;
  }

  .reviews__description {
    margin-top: 40px;
    width: 500px;
    font-size: 18px;
    line-height: 1.67;

    &--desktop {
      width: 528px;
      margin-top: 25px;
    }
  }

  .reviews__button {
    margin-bottom: 200px;
  }
}
